import api from "@/utils/api";

export default async function PaymentResumePage() {
  try {
    const user = await api.user.fetch(); // Llamada a la función fetch
    const authorizationUrl = await api.user.authorize();

    return (
      <div>
        <h1>Resumen de Pago</h1>
        <p>Bienvenido, {user.name}</p>
        <a href={authorizationUrl}>Autorizar con MercadoPago</a>
      </div>
    );
  } catch (error) {
    console.error("Error al cargar los datos:", error);
    return <p>Hubo un error al cargar los datos. Por favor, intenta de nuevo.</p>;
  }
}
